import { NgModule} from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { SchedulingSetupComponent } from "./scheduling-setup/scheduling-setup.component";

const routes: Routes = [
  { path: "scheduling-setup", component: SchedulingSetupComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class CurriculaSetupRoutingModule {}
