import { Component, OnInit, ViewChild } from "@angular/core";
import { NgxSpinnerService } from "ngx-spinner";
import { MessagesService } from "app/shared/services/messages.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { MatDialog } from "@angular/material/dialog";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { TranslateService } from "@ngx-translate/core";
import { SechedulingSetupService } from "app/dashboard/services/setup/curricula-setup/secheduling-setup.service";
import { ISchedulingSetup } from "app/dashboard/models/setup/curricula-setup/scheduling-setup";
import { CourseGetVM } from "app/dashboard/models/setup/educational-setup/course";
import { CourseService } from "app/dashboard/services/setup/educational-setup/course.service";
import { EducationYearService } from "app/dashboard/services/setup/educational-setup/education-year.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { ActivatedRoute } from "@angular/router";
import { AuthService } from "app/shared/services/auth/auth.service";
import { IEducationYear } from "app/dashboard/models/setup/educational-setup/education-year";
@Component({
  selector: "app-secheduling-setup",
  templateUrl: "./scheduling-setup.component.html",
  styleUrls: ["./scheduling-setup.component.scss"],
})
export class SchedulingSetupComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  curLang: string;
  courseList: CourseGetVM[];
  educationalYearList: IEducationYear[];
  EduCompId: number;
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  constructor(
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    private dialog: MatDialog,
    private translate: TranslateService,
    private fb: FormBuilder,
    private sechedulingSetupService: SechedulingSetupService,
    private courseService: CourseService,
    private educationYearService: EducationYearService,
    private route: ActivatedRoute,
    public authService: AuthService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              this.rowFunctions = res;
            }
          });
      }
    });
    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      ID: [0],
      LectuerPerDay: ["", [Validators.required, CustomeValidator.bigZero]],
      LectuerPerWeek: ["", [Validators.required, CustomeValidator.bigZero]],
      DayPerWeek: ["", [Validators.required, CustomeValidator.bigZero]],
      CourseId: [, [Validators.required]],
      EducationalId: [, [Validators.required]],
    });

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
    this.returnAllScheduals(1);
    this.getAllCourses();
    this.getAllEducationalYears();
  }

  ngAfterViewInit() {
    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.returnAllScheduals(1);
    });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  editSchedual(data: ISchedulingSetup) {
    this.myForm.patchValue(data);
  }

  // Start: Return All Courses

  getAllCourses() {
    this.courseService.getAllCoursesDropDown().subscribe((res: any) => {
      if (res) {
        this.courseList = res;
      }
    });
  }

  // Start: Return All Educational years

  getAllEducationalYears() {
    this.educationYearService
      .getEducationalYearDropdown()
      .subscribe((res: any) => {
        if (res) {
          this.educationalYearList = res;
        }
      });
  }

  // Start: Return All Scheduals
  returnAllScheduals(page: number) {
    this.sechedulingSetupService
      .getScheduals(page)
      .subscribe((res) => {
        if (res) {
          // this.dataSource = new MatTableDataSource(res.SupplierAndCustomerList);
          // this.itemsCounts = res.itemCount;
        }
      });
  }
  onSubmit() {
    this.submitted = true;

    let Data: ISchedulingSetup = {
      ID: this.FormCtrls.id.value,
      EducationalId: this.FormCtrls.EducationalId.value,
      CourseId: this.FormCtrls.CourseId.value,
      LectuerPerDay: this.FormCtrls.Telephone.value,
      LectuerPerWeek: this.FormCtrls.LectuerPerWeek.value,
      DayPerWeek: this.FormCtrls.DayPerWeek.value,
    };

    if (this.myForm.valid) {
      Data.ID
        ? this.addSupplierOrCustomer(Data)
        : this.updateSupplierOrCustomer(Data);
    } else this.toastr.error("General.FormNotValid");
  }
  // Start: Add Scheduals
  addSupplierOrCustomer(data: ISchedulingSetup) {
    this.sechedulingSetupService.addSchedual(data).subscribe((res: any) => {
      if (res.returnValue == 1) {
        this.spinner.show();
        //this.dataSource.data.push(res);
        this.submitted = true;
        this.myForm.reset();
        this.toastr.success("General.AddSuccessfully");
        this.spinner.hide();
      }
    });
  }

  // Start: Update    Scheduals

  updateSupplierOrCustomer(data: ISchedulingSetup) {
    this.sechedulingSetupService.updateSchedual(data).subscribe((res: any) => {
      if (res.returnValue == 1) {
        this.spinner.show();
        // let i = this.dataSource.data.findIndex((e) => e.Id == body.Id);
        // this.dataSource.data[i] = res;
        this.submitted = true;
        this.myForm.reset();
        this.FormCtrls.ID.setValue(0);
        this.toastr.success("General.UpdateSuccessfully");
        this.spinner.hide();
      }
    });
  }
  openDeleateDialog(data: ISchedulingSetup): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: data.ID,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteSchedual(data);
      });
  }
  // Start:Delete Scheduals
  deleteSchedual(data: ISchedulingSetup) {
    this.sechedulingSetupService.deleteSchedual(data.ID).subscribe((res: any) => {
      if (res.returnValue == 1) {
        // let index = this.dataSource.data.findIndex((e) => e == data);
        // this.dataSource.data.splice(index);

        this.toastr.success("General.DeleteSuccessfully");
      } else {
        this.toastr.error("General.TosterErrorMessage");
      }
    });
  }
}
