import { NgxSpinnerModule } from "ngx-spinner";
import { ReactiveFormsModule } from "@angular/forms";
import { MaterialModule } from "app/shared/material/material.module";
import { SharedModule } from "app/shared/shared.module";
import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { SchedulingSetupComponent } from "./scheduling-setup/scheduling-setup.component";
import { CurriculaSetupRoutingModule } from "./curricula-setup-routing.module";
import { DateAdapter, MAT_DATE_FORMATS } from "@angular/material/core";
import {
  AppDateAdapter,
  APP_DATE_FORMATS,
} from "app/shared/validators/date.adapter";

@NgModule({
  declarations: [SchedulingSetupComponent],
  imports: [
    CommonModule,
    NgxSpinnerModule,
    MaterialModule,
    SharedModule,
    ReactiveFormsModule,
    CurriculaSetupRoutingModule,
  ],
  providers: [
    {
      provide: DateAdapter,
      useClass: AppDateAdapter,
    },
    {
      provide: MAT_DATE_FORMATS,
      useValue: APP_DATE_FORMATS,
    },
  ],
})
export class CurriculaSetupModule {}
