import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { environment } from "environments/environment";
import { ISchedulingSetup } from "app/dashboard/models/setup/curricula-setup/scheduling-setup";
@Injectable({
  providedIn: 'root'
})
export class SechedulingSetupService {

  constructor(private http: HttpClient) {}

  // Return All Scheduals
  getScheduals(page: number) {

    return this.http.get(
      `${environment.baseUrl}/SchoolSystem/Schedual/getScheduals/${page}`
    );
  }



  //Add New Schedual
  addSchedual(data: ISchedulingSetup) {
    return this.http.post(
      `${environment.baseUrl}/SchoolSystem/Schedual/addSchedual`,
      data
    );
  }

  // Update Schedual
  updateSchedual(data: ISchedulingSetup) {
    return this.http.put(
      `${environment.baseUrl}/SchoolSystem/Schedual/editSchedual/${data.ID}`,
      data
    );
  }

  //Delete Scheduals
  deleteSchedual(SchedualId: number) {
    return this.http.delete(
      `${environment.baseUrl}/SchoolSystem/Schedual/deleteSchedual/${SchedualId}`
    );
  }
}
